'use client';
import { useEffect, useState } from 'react';
import { moviesApi, showsApi } from '@/lib/api';
import { Card, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import Image from 'next/image';

interface Movie {
  id: string;
  title: string;
  description: string;
  genre: string[];
  language: string;
  duration: number;
  release_date: string;
  poster_url: string;
  backdrop_url: string;
  rating: number;
}

export default function MoviesPage() {
  const [movies, setMovies] = useState<Movie[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    fetchMovies();
  }, []);

  const fetchMovies = async () => {
    try {
      const data = await moviesApi.getAll();
      setMovies(data);
    } catch (err: any) {
      setError(err.message || 'Failed to load movies');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-xl">Loading movies...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-red-600">Error: {error}</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-7xl mx-auto">
        <h1 className="text-4xl font-bold mb-8">Now Showing</h1>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
          {movies.map((movie) => (
            <Card key={movie.id} className="overflow-hidden hover:shadow-xl transition-shadow">
              <div className="relative h-96">
                {movie.poster_url ? (
                  <Image
                    src={movie.poster_url}
                    alt={movie.title}
                    fill
                    className="object-cover"
                  />
                ) : (
                  <div className="w-full h-full bg-gradient-to-br from-purple-400 to-blue-500 flex items-center justify-center">
                    <span className="text-white text-2xl font-bold">{movie.title[0]}</span>
                  </div>
                )}
                <div className="absolute top-2 right-2 bg-yellow-400 text-black px-2 py-1 rounded-md font-bold">
                  ⭐ {movie.rating}
                </div>
              </div>
              <CardContent className="p-4">
                <h3 className="text-xl font-bold mb-2">{movie.title}</h3>
                <div className="flex flex-wrap gap-2 mb-3">
                  {movie.genre?.map((g) => (
                    <span key={g} className="text-xs bg-gray-200 px-2 py-1 rounded">
                      {g}
                    </span>
                  ))}
                </div>
                <p className="text-sm text-gray-600 mb-2">
                  {movie.language} • {movie.duration} mins
                </p>
                <p className="text-sm text-gray-700 mb-4 line-clamp-2">
                  {movie.description}
                </p>
                <Link href={`/booking?movie_id=${movie.id}`}>
                  <Button className="w-full">Book Tickets</Button>
                </Link>
              </CardContent>
            </Card>
          ))}
        </div>

        {movies.length === 0 && (
          <div className="text-center text-gray-500 mt-12">
            No movies available at the moment.
          </div>
        )}
      </div>
    </div>
  );
}
